* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

header {
  z-index: 100;
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa-arrow-left {
  padding-right: 1rem;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.signout-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover {
  color: #6b2da8;
}

.signout-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: auto;
  padding: 5rem 12%;
  display: flex;
}

.row1 {
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
  max-height: 265px;
  display: flex;
}

.row2 {
  gap: 1rem;
  width: 100%;
  max-height: 400px;
  display: flex;
}

.select-container {
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  display: flex;
}

.dropdown {
  color: #8d8d8d;
  border: 1px solid #d3d3d3;
  border-radius: .5rem;
  outline: none;
  padding: .3rem .5rem;
  transition: all .3s;
}

.type-options, .months-option, .day-option, .linegraph-type-options, .linegraph-months-options, .linegraph-day-option {
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  transform: translateY(20px);
}

.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

option {
  color: #8d8d8d;
}

.bargraph {
  background-color: #efefef;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 520px;
  max-height: 265px;
  padding: 1rem;
  display: flex;
}

.users-total {
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: 1rem;
  gap: 1rem;
  width: 100%;
  height: 265px;
  padding: 2rem;
  display: flex;
}

.total-users, .students-inSchool {
  background-color: #efefef;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  display: flex;
}

.users-total h1, .users-total p {
  color: #6b2da8;
}

.users-total p {
  font-size: .8rem;
  font-weight: 700;
}

.doughnut-graph-container {
  background-color: #efefef;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  width: 265px;
  height: 265px;
  padding: 1rem;
  display: flex;
  position: relative;
}

.label-doughnut {
  color: #6b2da8;
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
  margin-top: 5px;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.line-graph-container {
  background-color: #efefef;
  border-radius: 1rem;
  flex-direction: column;
  width: 780px;
  height: auto;
  padding: 1rem;
  display: flex;
}

.blogs {
  width: 30%;
  height: 100%;
}

#idvalidated-graph {
  z-index: 10;
  position: relative;
}

.student-log-container {
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: 1rem;
  flex-direction: column;
  gap: 1rem;
  width: 500px;
  height: 100%;
  padding: 1rem;
  display: flex;
}

.form-container input {
  background-color: #ffffffc8;
  border: none;
  border-radius: .5rem;
  outline: none;
  padding: .2rem .3rem;
}

.form-container input:focus {
  color: #3f3e41;
  background-color: #ffffffc8;
  border: none;
  outline: none;
}

.studentNumber {
  width: 6.8rem;
}

.date {
  width: 9rem;
}

.fa-magnifying-glass {
  color: #6b2da8;
  padding: .2rem;
}

.search-student-form button {
  cursor: pointer;
  border: none;
  border-radius: .2rem;
  transition: all .3s;
}

.search-student-form button:hover {
  transform: scale(1.05);
}

.log-container {
  flex-direction: column;
  justify-content: center;
  gap: .5rem;
  width: 100%;
  height: auto;
  display: flex;
  overflow-x: auto;
}

.log-container h2 {
  color: #f0eff1;
}

.student-entryTime {
  color: #39b385;
  font-size: .7rem;
  font-weight: 700;
}

.student-exitTime {
  color: #e52a5a;
  font-size: .7rem;
  font-weight: 700;
}

.logDate {
  font-weight: 700;
}

.logs {
  background-color: #e2dfe3;
  border-radius: .5rem;
  width: 100%;
  padding: .3rem;
  transition: all .3s;
  overflow-y: auto;
}

.logs:hover {
  background-color: #f0eff1;
}

@media (width <= 480px) {
  .fa-arrow-left {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .row1, .row2 {
    flex-direction: column;
    gap: .5rem;
    width: 100%;
    height: auto;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .bargraph {
    max-width: 327px;
    max-height: 240px;
  }

  .doughnut-graph-container {
    justify-content: center;
    align-items: center;
    width: 327px;
    height: 327px;
    display: flex;
    position: relative;
  }

  .line-graph-container {
    max-width: 327px;
    height: auto;
    max-height: 260px;
  }

  .student-log-container {
    max-width: 327px;
    max-height: 250px;
    overflow-y: auto;
  }

  .users-total {
    height: 300px;
    padding: 1rem;
  }

  .total-users, .students-inSchool {
    border-radius: .5rem;
    height: 100px;
    padding: .5rem;
  }

  section {
    flex-direction: column;
    gap: 27rem;
    width: 100%;
    height: auto;
    padding: 5rem 8%;
    display: flex;
  }
}
